<template>
  <div>
    <CRow :gutters="false">
      <CCol md="12">
        <CCard class="mb-1 mr-1 cardstyle">
          <CCardBody>
            <CRow>
              <CCol sm="8" class="mb-0">
                <h4 id="traffic" class="card-title mb-1">減速提前值</h4>
              </CCol>
              <CCol sm="4" class="d-none d-md-block text-center">
                
              </CCol>
            </CRow>
            <LineChart v-if="loaded" ref="lineChart" :styles="myStyles" :data="chartData"></LineChart>
          </CCardBody>
          <CCardFooter style="background:#DFFEFF">
            <CRow class="text-center">
              <CCol md="4" sm="12" class="mb-sm-2 mb-0">
                <h5>捲取啟動</h5>
                <h5 v-if="reeling"><div class="stop">停止</div></h5>
                <h5 v-else><div class="run">啟動</div></h5>
              </CCol>
              <CCol md="4" sm="12" class="mb-sm-2 mb-0">
                <h5>退線啟動</h5>
                <h5 v-if="retreat"><div class="stop">停止</div></h5>
                <h5 v-else><div class="run">啟動</div></h5>
              </CCol>
              <CCol md="4" sm="12" class="mb-sm-2 mb-0">
                <h5>減速提前值</h5>
                <h5 class="value">{{speed}}</h5>
              </CCol>
            </CRow>
          </CCardFooter>
        </CCard>
      </CCol>
    </CRow>
    <CRow :gutters="false">
      <CCol md="12" sm="12">
        <CCard class="mr-1 mb-1 h-100 cardstyle">
          <CCardBody>
            <MainTable
              :items="arrErrMsg"
              bordered
              small
              caption="異常紀錄"
              class="mx-1 my-1 h-100 text-center"
            />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  </div>
</template>

<script>
import MainTable from '@/views/table/Table.vue'
import LineChart from '@/views/charts/LineChart'
import { getTimeStr } from '@/common/utils.js'


export default {
  name: 'Dashboard',
  props: ['mac'],
  components: {
    MainTable,
    LineChart
  },
  data() {
    return {
      timer: null,
      reeling: 0,         // 0:捲取啟動, 1:捲取停止
      retreat: 0,         // 0:退線啟動, 1:退線停止
      speed: '',          // 減速提前值
      status: 0,           // 0:啟動, 1:停機
      arrLiveErrMsg: [],   // 即時異常訊息陣列
      arrErrMsg: [],       // 今日異常訊息陣列
      arrSpeed: [],        // 減速提前值陣列
      arrTime: [],         // 時間
      arrLabel: [],
      height: 400,
      chartData: null,
      loaded: false,
    }
  },
  computed: {
    myStyles () {
      return {
        height: `${this.height}px`,
      }
    }
  },
  mounted() {
    this.initGetTodayData()
    // 每秒更新最新的資料
    this.timer = setInterval(() => {
      // 取得設備最新資料
      this.$http.get(`/api/SpoolingLastData/${this.mac}`)
      .then(res => {
        if(res.data.length>0) {
          this.reeling = res.data[0].CH6_10
          this.retreat = res.data[0].CH6_09
          this.speed = res.data[0].DM601
          
          let arrErr = res.data[0].arrError

          if(res.data[0].time > Math.max(...this.arrTime)) {
            this.arrSpeed.push(res.data[0].DM601)     // 減速提前值陣列
            this.arrTime.push(res.data[0].time)      // 
            this.arrLabel.push(getTimeStr(res.data[0].time))
            
            // !!!這邊不需要再push資料到chartData，因為this.chartData.datasets[0].data已ref到this.arrSpeed, 否則會多一筆資料
            // this.chartData.datasets[0].data.push(res.data[0].DM601)
            // this.chartData.labels.push(getTimeStr(res.data[0].time))

            this.$refs.lineChart.update()  // update chart

            arrErr.forEach(element => {
              this.arrErrMsg.splice(0, 0, element)  // 將資料插入最前面
            })
          }
          // check exception
          this.arrLiveErrMsg = []
          for(let i=0; i<arrErr.length; i++) {
            this.arrLiveErrMsg.push(arrErr[i].msg) 
          }

          this.$emit('getStatus', this.status, this.arrLiveErrMsg)
        }
      })
      .catch(err => console.log(err.response.data.message))

    }, 1000)
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  methods: {
    initGetTodayData() {
      // 取得今日當下所有資料
      this.$http.get(`/api/Spooling/${this.mac}/${20200520000000}/${20200520235959}`)
      .then(res => {
        this.loaded = true
        this.arrSpeed = []
        this.arrTime = []
        this.arrErrMsg = []
        this.arrLabel = []
        
        for(let i=0; i<res.data.length; i++) {
          this.arrSpeed.push(res.data[i].DM601)     
          this.arrTime.push(res.data[i].time)
          this.arrLabel.push(getTimeStr(res.data[i].time))    

          let arrErr = res.data[i].arrError
          arrErr.forEach(element => {
            this.arrErrMsg.push(element)
          })
        }

        this.getChartData()
        
        // 資料依時間由新到舊進行array reverse
        this.arrErrMsg.reverse()
      })
      .catch(err => console.log(`initGetTodayData(): ${err}`))
    },
    getChartData() {
      this.chartData = Object.assign({})
      this.$set(this.chartData, "labels", this.arrLabel)
      this.$set(this.chartData, "datasets", [])
      this.chartData.datasets.push({
        label: '減速提前值',
        data: this.arrSpeed,
        backgroundColor: '#e4f9ff',
        borderColor: '#11cbd7',
        borderWidth: 2
      })
    },
  }
}
</script>

<style lang="scss" scoped>
.cardstyle {
  // border: 0px;
  box-shadow: 2px 2px 2px rgba(20%,20%,40%,0.1),4px 4px 6px rgba(20%,20%,40%,0.1),6px 6px 12px rgba(20%,20%,40%,0.2);
}

.run {
  background:#63F53F;
  padding:4px;
  color:#04073b;
  width:120px;
  display:inline-block;
}

.stop {
  background:#9da1a1;
  padding:4px;
  color:#04073b;
  width:120px;
  display:inline-block;
}

.value {
  background:#f6f578;
  padding:4px;
  width:120px;
  display:inline-block;
}

</style>


